import React from 'react';
import ReactDOM from 'react-dom';
import "./index.css"
import fishlists from "./fishlists.js"





class FishLists extends React.Component {
    render() {
        return (
            <div className="fishLists">
                <FishList listName="Pedalskie Ryby" fishComponents={BadFishComponents} fishAmount={fishlists.BadFish.length}/>
                <FishList listName="Niepedalskie Ryby" fishComponents={GoodFishComponents} fishAmount={fishlists.GoodFish.length}/>
                <FishList listName="OG Ryby" fishComponents={OGFishComponents} fishAmount={fishlists.OGFish.length}/>
            </div>
        )
    }
}
class FishList extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            expanded: false
        }
        this.hideOrExpand = this.hideOrExpand.bind(this)
    }
    hideOrExpand() {
        if(this.state.expanded) {
            this.setState({
                expanded: false
            })
        }
        else {
            this.setState({
                expanded: true
            })
        }
    }
    render() {
        return (
            <div className="fishList" style={{
                height: this.state.expanded ? `${this.props.fishAmount * 13}vh` : "3vh",
                minHeight: this.state.expanded ? "36vh" : "0"
                }}>
                <div className="listHeader">
                    <ListShowButton clickFunc={this.hideOrExpand} expanded={this.state.expanded}/>
                    <span className="listName">{this.props.listName}</span>
                </div>
                <div className="fishes">
                        {this.props.fishComponents}
                    </div>
            </div>
        )
        
    }
}
class Fish extends React.Component {
    render() {
        return (
        <div className="fish">
            <img className="fishImg" src={this.props.imgUrl} />
            <div className="fishName">{this.props.name}</div>
        </div>
        )
    }
}
class ListShowButton extends React.Component {
    render() {
        return (
            <button className="showButton" onClick={() => this.props.clickFunc()}>
                {this.props.expanded ? "Zwiń" : "Rozwiń"}
            </button>
        )
    }
}







class LightDarkModeButton extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            darkMode: localStorage.getItem("darkMode") == "true" ? true : false
        }
    }
    lightModeColors = {
        backgroundColor:"#c9d6df",
        subBackgroundColor: "#f0f5f9",
        textColor: "#1e2022",
        shadowColor: "rgba(0,0,0,0.25)"
    }
    darkModeColors = {
        backgroundColor:"#171A21",
        subBackgroundColor: "#617073",
        textColor: "#bed6db",
        shadowColor: "rgba(200,200,200,0.25)"
    }
    ToggleLightDarkMode() {
        if(this.state.darkMode) {
            localStorage.setItem("darkMode", false)
            this.setState({
                darkMode: false
            })
            
        }
        
        else {
            localStorage.setItem("darkMode", true)
            this.setState({
                darkMode: true
            })
            
        }
        
    }
    componentDidMount() {
        this.componentDidUpdate()
    }
    componentDidUpdate() {
        if(this.state.darkMode) {
            document.documentElement.style.setProperty('--background-color', this.darkModeColors.backgroundColor);
            document.documentElement.style.setProperty('--sub-background-color', this.darkModeColors.subBackgroundColor);
            document.documentElement.style.setProperty('--text-color', this.darkModeColors.textColor);
            document.documentElement.style.setProperty('--shadow-color', this.darkModeColors.shadowColor);
        }
        else {
            document.documentElement.style.setProperty('--background-color', this.lightModeColors.backgroundColor);
            document.documentElement.style.setProperty('--sub-background-color', this.lightModeColors.subBackgroundColor);
            document.documentElement.style.setProperty('--text-color', this.lightModeColors.textColor);
            document.documentElement.style.setProperty('--shadow-color', this.lightModeColors.shadowColor);
        }
    }

    render() {
        return (
            // <button onClick={() => this.ToggleLightDarkMode()}>
            //     {this.state.darkMode ? "Tryb dzienny" : "Tryb Nocny"}
            // </button>
            <div class="sliderContainer">
                <div className="sliderBackground" onClick={() => this.ToggleLightDarkMode()}>
                    <div className="sliderCircle" style={{
                        transform: this.state.darkMode ? "translate(7vh)" : ""
                    }}>
                    
                    </div>
                </div>
                <div className="sliderLabel">
                    {this.state.darkMode ? "Tryb nocny" : "Tryb dzienny"}
                </div>
            </div>
        )
    }
}
class Header extends React.Component {
    render() {
        return (
        <header class="header">
            <div class="siteTitle">
                Ultimate Lista Ryb
            </div>
            <LightDarkModeButton />
        </header>
        )
    }
    
}

class App extends React.Component {
    render() {
        return (
            <div>
                <Header />
                <FishLists />
            </div>
        )
    }
}

let OGFishComponents = fishlists.OGFish.map((el) => {
    return <Fish imgUrl={el.imgUrl} name={el.name}/>
})
let BadFishComponents = fishlists.BadFish.map((el) => {
    return <Fish imgUrl={el.imgUrl} name={el.name}/>
})
let GoodFishComponents = fishlists.GoodFish.map((el) => {
    return <Fish imgUrl={el.imgUrl} name={el.name}/>
})

ReactDOM.render((<App />), document.getElementById('root'))